import { Component, Prop, h, Event, EventEmitter } from '@stencil/core';

@Component({
  tag: 'promerica-button',
  styleUrl: 'promerica-button.css',
  shadow: true, // Usa Shadow DOM para encapsular los estilos
})
export class PromericaButton {

  /**
   * Texto que se mostrará dentro del botón.
   */
  @Prop() label: string;

  /**
   * Determina la variante del botón (primary, secondary, danger).
   */
  @Prop() variant: 'primary' | 'secondary' | 'danger' = 'primary';

  /**
   * Si es true, el botón se deshabilita.
   */
  @Prop() disabled: boolean = false;

  /**
   * Evento emitido cuando el usuario hace clic en el botón.
   */
  @Event() btnClick: EventEmitter<void>;

  private handleClick = () => {
    if (!this.disabled) {
      this.btnClick.emit();
    }
  };

  render() {
    // Usamos las propiedades 'variant' y 'disabled' como clases CSS para estilizar
    return (
      <button
        class={`btn-base btn-${this.variant}`}
        disabled={this.disabled}
        onClick={this.handleClick}
      >
        {this.label || 'Botón'}
      </button>
    );
  }
}

